import { Component, OnInit } from '@angular/core';
import { PayrollBankAccountService } from 'src/app/sdk/payroll/services/payroll-bank-account.service';
import { Bank_account } from 'src/app/sdk/payroll/models/bank-_account';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { PegawaiDetailService } from '../../../../service/pegawai-detail.service';
import { NzMessageService } from 'ng-zorro-antd';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private payrollBankAccountService: PayrollBankAccountService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private pegawaiDetailService: PegawaiDetailService,
        private nzMessageService: NzMessageService
    ) { }

    id_pegawai: string = ''
    dataBankAccount: Bank_account[] = []
    untuk: string = 'payroll'
    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })
        this.getBankAccount()
    }


    getBankAccount() {
        let param = {
            id_pegawai: this.id_pegawai,
            untuk: this.untuk
        }
        this.payrollBankAccountService.getPayrollBankAccounts(JSON.stringify(param)).subscribe(
            data => {
                this.dataBankAccount = data.data
                this.spinner.hide()
            }
        )
    }

    delete(id) {
        this.spinner.show()
        this.payrollBankAccountService
            .deletePayrollBankAccountsId(id)
            .subscribe(data => {
                this.getBankAccount()
                this.spinner.hide()
            })

    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }

}
