import { Component, OnInit } from '@angular/core';
import { PayrollBankAccountService } from 'src/app/sdk/payroll/services';
import { Bank_account } from 'src/app/sdk/payroll/models';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PegawaiDetailService } from '../../../../service/pegawai-detail.service';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private payrollBankAccountService: PayrollBankAccountService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pegawaiDetailService: PegawaiDetailService,
        private pesan: PesanService,
        private router: Router,
    ) { }

    id_pegawai: string = ''
    dataBankAccount: Bank_account

    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            this.getBankAccount()
        })
        this.form()
    }

    dataBankAccount_cek = [];
    getBankAccount() {
        let param = {
            id_pegawai: this.id_pegawai,
            untuk: 'payroll'
        }
        this.payrollBankAccountService.getPayrollBankAccounts(JSON.stringify(param)).subscribe(
            data => {
                this.dataBankAccount_cek = data.data
                this.spinner.hide()
            }
        )
    }

    dataForm: FormGroupTypeSafe<Bank_account>
    form() {
        this.dataForm = this.fb.group<Bank_account>({
            nama_bank: new FormControl('', [Validators.required]),
            no_rekening: new FormControl(''),
            jumlah: new FormControl(''),
            cabang_bank: new FormControl(''),
            status_rekening: new FormControl('Active'),
            untuk: new FormControl('payroll'),
            id_pegawai: new FormControl(this.id_pegawai),
        })
    }

    getValue(key) {
        return this.dataForm.get(key).value
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataBankAccount = this.dataForm.value
        this.payrollBankAccountService
            .postPayrollBankAccounts(this.dataBankAccount)
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/pegawai-detail', this.id_pegawai, 'payroll', 'bank', 'list', 'gaji', 'list'])
    }

}
