import { Component, OnInit } from '@angular/core';
import { PayrollBankAccountService } from 'src/app/sdk/payroll/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PegawaiDetailService } from '../../../../service/pegawai-detail.service';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';
import { Bank_account } from 'src/app/sdk/payroll/models';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private payrollBankAccountService: PayrollBankAccountService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pegawaiDetailService: PegawaiDetailService,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
    ) { }

    id_pegawai: string = ''
    id_bank_account: string = ''
    dataBankAccount: Bank_account

    jumlah = 0;
    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })
        this.id_bank_account = this.activatedRoute.snapshot.paramMap.get('id_bank_account')

        this.form()
        this.getData()
    }

    getData() {
        this.payrollBankAccountService.getPayrollBankAccountsId(this.id_bank_account).subscribe(
            data=> {
                this.dataForm.setValue(data.data)
                this.jumlah = data.data.jumlah
            }
        )
    }

    dataForm: FormGroupTypeSafe<Bank_account>
    form() {
        this.dataForm = this.fb.group<Bank_account>({
            nama_bank: new FormControl('', [Validators.required]),
            no_rekening: new FormControl(''),
            id_bank_account: new FormControl(''),
            jumlah: new FormControl(''),
            type_rekening: new FormControl(''),
            id_dokumen: new FormControl(''),
            id_user: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            cabang_bank: new FormControl(''),
            status_rekening: new FormControl(''),
            no_dokumen: new FormControl(''),
            untuk: new FormControl('payroll'),
            id_pegawai: new FormControl(this.id_pegawai),
        })
    }

    getValue(key) {
        return this.dataForm.get(key).value
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataBankAccount = this.dataForm.value
        this.payrollBankAccountService
            .putPayrollBankAccountsId({id: this.id_bank_account, body: this.dataBankAccount})
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/profile/payroll/bank/list/gaji/list'])
    }

}
